<template>
    <article class="showcase">
      <swiper :options="swiperOption" ref="mySwiper" @someSwiperEvent="callback">
        <!-- slides -->
          <swiper-slide v-for="items in mobile_case" :key="items.id">
            <ul class="case">
              <li v-for="item in items" :key="item.id">
                <div class="case__img--wrapper">
                  <div class="case__link"></div>
                  <div class="case__img">
                    <img :src="item.img" alt="">
                  </div>
                  <div class="case__link">
                    <router-link
                      :to="`/model/${item.id}`"
                      tag="div"
                      class="case__link--wrapper"
                      :exact="true"
                    >
                      <div class="col-6">
                         
                      </div>
                      <div class="col-6">
                        <img src="img/tnm.png" alt="">
                      </div>
                    </router-link>
                  </div>
                </div>
                <div class="case__info">
                  <h2 class="case__title">
                    {{ item.type }}
                  </h2>
                  <h4 class="case__model">
                    {{ item.model }}
                  </h4>
                </div>
              </li>
            </ul>
          </swiper-slide>
        <!-- Optional controls -->
        <div class="swiper-pagination"  slot="pagination"></div>
      </swiper>

      <!--<modal-->
        <!--v-if="modal.showModal"-->
        <!--@showModal="closeModal"-->
        <!--:DataRow="modal"-->
      <!--&gt;</modal>-->
    </article>
</template>

<script>
  import { swiper, swiperSlide } from 'vue-awesome-swiper';
  // import modal from '../modal/CaseDetailsModal.vue';

  export default {
    name: "Showcase",
    components: {
      swiper,
      swiperSlide,
      // modal,
    },
    data() {
      return {
        // modal: {
        //   showModal: false,
        //   type: null,
        //   case: null
        // },
        mobile_case: [
          {
            id: 'dark_black',
            img: 'img/case/black.png',
            type: 'apple iphone',
            model: 'dark black'
          },
          {
            id: 'deep_brown',
            img: 'img/case/yellow.png',
            type: 'apple iphone',
            model: 'bright yellow'
          },
          {
            id: 'brown',
            img: 'img/case/brown.png',
            type: 'apple iphone',
            model: 'deep brown'
          },
          {
            id: 'dark',
            img: 'img/case/black.png',
            type: 'apple iphone',
            model: 'dark black'
          },
          {
            id: 'deep',
            img: 'img/case/yellow.png',
            type: 'apple iphone',
            model: 'deep brown'
          },
        ],
        swiperOption: {
          loop: false,
          centeredSlides: true,
          // autoplay: {
          //   delay: 5000,
          //   disableOnInteraction: false
          // },
          direction: 'vertical',
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          }
        }
      }
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    mounted() {
      this.swiper.slideTo(0, 1000, false);

      this.mobile_case = this.lodash.chunk( this.mobile_case , 3);
      // console.log( this.mobile_case )
    },
    methods: {
      callback(e) {
        // console.log( e )
      },
      // showCaseDetails(e, type) {
      //   // console.log( e )
      //
      //   // console.log( this.closet(e.target, '.case__link--wrapper').dataset.link )
      //   this.modal.case = e;
      //   this.modal.type = type;
      //
      //   document.getElementsByTagName('BODY')[0].style.overflow = 'hidden';
      //
      //   this.modal.showModal = this.modal.showModal ? false : true;
      // },
      closet (elem, selector) {
          if (!Element.prototype.matches) {
              Element.prototype.matches =
                  Element.prototype.matchesSelector ||
                  Element.prototype.mozMatchesSelector ||
                  Element.prototype.msMatchesSelector ||
                  Element.prototype.oMatchesSelector ||
                  Element.prototype.webkitMatchesSelector ||
                  function(s) {
                      var matches = (this.document || this.ownerDocument).querySelectorAll(s),
                          i = matches.length;
                      while (--i >= 0 && matches.item(i) !== this) {}
                      return i > -1;
                  };
          }
          for ( ; elem && elem !== document; elem = elem.parentNode ) {
              if ( elem.matches( selector ) ) return elem;
          }
          return null;
      },
      // closeModal() {
      //     // console.log(' close in devices ')
      //     this.modal.showModal = false;
      //     document.getElementsByTagName('BODY')[0].style.overflow = 'auto';
      // },
    }
  }
</script>

<style lang="scss">
  /*@import '../../../../node_modules/swiper/dist/css/swiper.css';*/

  .swiper {
    &-container {
      height: 100vh;
    }
    &-pagination-bullet {
      background: transparent;
      border: 2px solid #fff;
      opacity: 1;
      width: 10px;
      height: 10px;

      &-active {
        background-color: #fff;
      }
    }
  }

  .case {
    /*font-size: 200%;*/
    display: flex;
    list-style: none;
    margin: 0 auto;
    // width: 96%;
    padding-left: 0;
    color: #ffffff;
    justify-content: center;
    width: 97%;
    // width: 100%;
    height: 100vh;
    &__img {
      position: relative;
      width: 50%;
      &:after {
        content: '';
        display: block;
        width: 10px;
        height: 10px;
        background-color: transparent;
        position: absolute;
        top: 50%;
        z-index: -1;
        left: 50%;
        border-radius: 50%;
        box-shadow: 0px 0px 15rem 10rem rgba(255,255,255,.3),
                    0px 0px 25rem 5rem rgba(255,255,255,.1);
        @media screen and (min-width: 1600px) {
          box-shadow: 0px 0px 25rem 15rem rgba(255,255,255,.3),
                      0px 0px 35rem 10rem rgba(255,255,255,.1);
        }
         @media screen and (min-width: 1900px) {
          box-shadow: 0px 0px 30rem 15rem rgba(255,255,255,.3),
                      0px 0px 40rem 15rem rgba(255,255,255,.2),
                      0px 0px 45rem 10rem rgba(255,255,255,.1);
        }
      }
      &--wrapper {
        display: flex;
        width: 100%;
      }
    }
    &__link {
      display: flex;
      align-items: flex-end;
      width: 25%;
      /*width: 66px;*/
      /*height: 41px;*/
      color: #ffffff;
      font-family: "Oswald - Demi";
      font-size: 18px;
      font-weight: 700;
      font-style: italic;
      line-height: 24px;
      text-transform: uppercase;
      letter-spacing: 0.18px;
      @media screen and (max-width: 1600px) {
        font-size: 14px;
        line-height: 16px;
      }
      &:first-child {
        // width: 20%;
      }
      &:last-child {
        // width: 30%;
      }
      cursor: pointer;
      &--wrapper {
        opacity: 0.25;
        display: flex;
        align-items: center;
        transition: all .5s ease-in-out;
        &:hover {
          opacity: .5;
          .col-6 {
            &:last-child {
              // left: .5rem;
              transform: translate(1rem);
            }
          }
        }
        .col-6 {
          &:first-child {
            padding-left: 1rem;
          }
          &:last-child {
            transition: all .5s ease-in-out;
          }
        }
      }
    }
    &__info {
      position: relative;
      width: 100%;
      text-align: center;
    }
    &__title {
      // width: 346px;
      // height: 55px;
      color: #ffffff;
      font-family: "Oswald - Demi";
      font-size: 3.75rem;
      font-weight: 700;
      font-style: italic;
      // line-height: 59.16px;
      text-transform: uppercase;
      letter-spacing: 0.72px;
    }
    &__model {
      // width: 244px;
      // height: 62px;
      color: #b10000;
      font-family: "Oswald - Regular Italic";
      font-size: 3.25rem;
      font-weight: 400;
      // line-height: 39.54px;
      text-transform: uppercase;
      // letter-spacing: 2.24px;
          position: absolute;
      top: 2.5rem;
      transform: rotate(-3deg);
      right: 7%;
    }
    li {
      width: 33.33%;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      flex-direction: column;
      img {
        /*width: 70%;*/
      }
    }
  }

  .col-6 {
    width: 50%;
    position: relative;
  }
</style>



