<template>
    <main>
      <show-case></show-case>

      <discount></discount>

      <ad-unit></ad-unit>
    </main>
</template>

<script>
  import ShowCase from "../content/Showcase.vue";
  import Discount from "../content/Discount.vue";
  import AdUnit from "../content/AdUnit.vue";

  export default {
    name: "MainComponent",
    components: {
      AdUnit,
      Discount,
      ShowCase,
    },
    methods: {
    }
  }
</script>

<style lang="scss">
  /* width */
  ::-webkit-scrollbar {
    width: 20px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: red;
    border-radius: 10px;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #b30000;
  }
</style>



